













import { Component, Prop, Vue } from "vue-property-decorator";

@Component({
  components: {}
})
export default class ConfigPanel extends Vue {
  $store: any;
  $router: any;

  get examples(){
    return this.$store.state.examples;
  }

  mounted() {
  }

  gotoExample(name) {
    console.log("goto");
    this.$store.state.viewExample = name;
    this.$router.push({
      name: "example",
      params: {
        name
      }
    });
  }
}
