



























import { Component, Prop, Vue } from 'vue-property-decorator';
import ConfigItem from './item.vue';
@Component({
  name: 'config-folder',
  components:{
    ConfigItem
  }
})
export default class Folder extends Vue {
  expand:boolean = true;
  @Prop() config: any;

  get isEmptyList(){
    return this.config.value.length === 0
  }

  toggle(){
    if(!this.isEmptyList){
      this.expand = !this.expand;
    }
  }
}


