




















import { Component, Prop, Vue } from "vue-property-decorator";
import { SceneView, GeometryView } from "../../model/scene-view";

@Component({
  components: {
  }
})
export default class GeometryViewPanel extends Vue {
  @Prop() view: SceneView

  expandDetail = true;

  get geometrylist(){
    const list = [];
    this.view.geometries.forEach(geo =>{
      list.push(geo);
    })
    return list;
  }
}
